.outerContainer{
 display: flex;
 justify-content: center;
 align-items: center;
 height: 100vh;
 background-color: #1A1A1D;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 60%;
    width: 35%;
}

@media (min-width: 320px) and (max-width: 480px){
    .container {
        width: 100%;
        height: 100%;
    }
}

@media (min-width: 480) and (max-width: 1200){
    .container {
        width: 60%;        
    }
}
