.joinOuterContainer{
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background-color:gray;
}

.joinInnerContainer {
  width: 20%;
}

.joinInput{
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
}

.heading{
  color: white;
  font-size:2.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
}

.button{
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979FF;
  padding: 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  width: 115%;
}

.mt-20 {
  margin-top: 20px;
}

@media (min-width:320px) and (max-width: 480px){
  .joinInnerContainer{
    width: 90%;
  }
}

.button:focus{
  outline: 0;
}

.space{
  padding: 10px;  
}
.backg-border{
  background: aliceblue;  
}